.header {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  padding-left: 50px;
  padding-right: 50px;
  
  min-height: 20%;
  &__logo {
    position: relative;
    top: 40px;
    width: 500px;
    height: 200px;
  }
  &__nav {
    min-width: 400px;
    display: flex;
    flex-flow: column;
    justify-content: flex-end;
    &--menu {
      position: relative;
      display: flex;
      flex-flow: row;
      justify-content: flex-end;
      list-style: none;
    }
    & li {
      width: 150px;
      text-align: right;
    }
    &--link {
      text-decoration: none;
      
      
      color: $secondColor;
      height: auto;
      margin-left: 10px;
      margin-right: 10px;
      font-family: "Montserrat";
      font-weight: 200;
      font-size: 30px;
      
    }
  }
  & li:hover {
    cursor: pointer;
  }
}
