body,
html,
#root {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0px;
  padding: 0px;
}
.body {
  display: block;

  min-height: 100%;
  padding-bottom: 30px;
}
a {
  text-decoration: none;
  &:hover {
    cursor: pointer;
  }
}

.mainHome, .mainProduct, .mainContact {
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}
