.footer {
    position: relative;
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    background-color: $secondColor;
    min-height: 20%;
    color: white;
    
    &__container {
        width: 40%;
        margin-top: auto;
        margin-bottom: auto;
    }

    &__logo{
        position: relative;
        left: 30px;
        width: 350px;
        
    }
    &__adress {
        width: 30%;
        display: flex;
        flex-flow: column;
        font-family: "Montserrat";
        font-weight: 500;
        line-height: 10px;
        margin-top: auto;
        margin-bottom: auto;
        border-left: 1px solid white;
        padding-left: 20px;
    &--contact {
        display: flex;
        flex-flow: row;
        & i {
            width: 30px;
            color: white;
        }
    }}
    &__network {
        width: 30%;
        display: flex;
        flex-flow: column;
        justify-content: center;
        text-align: center;
        font-size: 35px;
        & a{
            color: white;
        }
        & :hover {
            cursor: pointer;
        }
        
        & i {
            width: 50%;
            padding: 20px;
            margin: 0 auto;
        }
        & p {
            font-family: "Montserrat";
            font-size: 20px;
            width: 50%;
            margin: 0 auto;
        }
    }
    }
